  const makeCrimesHediondosEnries = (quantity, startingId = 0, type) => {
    return Array(quantity)
      .fill(startingId)
      .reduce((acc, e, index) => {
        const num = e + index;
        const idNum = num < 10 ? `00${num}` : num < 100 ? `0${num}` : `${num}`;
        const id = `dmhk-${type}-${idNum}`;

        acc[id] = {
          id,
          type: type === 'wp' ? 'weapon' : 'evidence',
          name: {
            en: '',
            pt: '',
          },
        };

        return acc;
      }, {});
  };

